header{
  width: 100%;
  height: 50px;
  background-color: $color-white;
  border-bottom: 1px solid $color-white-gray;
  position: relative;
  z-index: 5;

  .inner{
    height: 100%;

    h1{
      height: 100%;

      a{
        height: 100%;
        display: flex;
        align-items: center;

        i{
          @include logo(30px);
          margin-right: 5px;
        }
  
        span{
          font-size: $font-size-medium;
          color: $color-black;
        }
      }
    }
  }
}